  /***********overlay********/
  
  .overlay-container {
    position: absolute;
    top: 0;
    left: 50%;
    width: 50%;
    height: 100%;
    overflow: hidden;
    transition: transform 0.6s ease-in-out;
    z-index: 100;
  }
  
  .FormsContainer.right-panel-active .overlay-container {
    transform: translateX(-100%);
  }
  .overlay {
    background: #fff;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 0 0;
    position: relative;
    left: -100%;
    height: 100%;
    width: 200%;
    transform: translateX(0);
    transition: transform 0.6s ease-in-out;
  }
  .FormsContainer.right-panel-active .sign-in-container {
    transform: translateX(100%);
    opacity: 0;
  }
  
  .FormsContainer.right-panel-active .sign-up-container {
    transform: translateX(100%);
    opacity: 1;
    z-index: 5;
    animation: show 0.6s;
    background-color:#0168B6; ;
  }
  
  @keyframes show {
    0%,
    49.99% {
      opacity: 0;
      z-index: 1;
    }
  
    50%,
    100% {
      opacity: 1;
      z-index: 5;
    }
  }
  
  .FormsContainer.right-panel-active .overlay {
    transform: translateX(50%);
  }
  .overlay-left {
    transform: translateX(-20%);
    border-right: 1.2px solid #b7b3b3;
    /* border-left: 25px solid #ed1c24; */
    background-image: url(../images/BG1.png);
  }
  
  .FormsContainer.right-panel-active .overlay-left {
